button.loginButtons,
button.loginButtons:hover {
  border-radius: 52px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  padding: 10px;
}

button.loginButtons:hover {
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
}

button.loginButton,
button.loginButton:hover {
  background-color: #F28F17;
}

button.resendToken,
button.resendToken:hover {
  background-color: #17c6f2;
}
